.page {
  page-break-after: always;
  position: relative;
}
.page .form-img {
  width: 100%;
}

.page-details.norfolk {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10000;
}
.page-details.norfolk input[type=date]::-webkit-inner-spin-button, .page-details.norfolk input[type=date]::-webkit-calendar-picker-indicator,
.page-details.norfolk input[type=year]::-webkit-inner-spin-button,
.page-details.norfolk input[type=year]::-webkit-calendar-picker-indicator,
.page-details.norfolk input[type=month]::-webkit-inner-spin-button,
.page-details.norfolk input[type=month]::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
}
.page-details.norfolk .form-img {
  width: 100%;
}
.page-details.norfolk .answer {
  position: absolute;
  bottom: 0;
}
.page-details.norfolk .answer img {
  width: 100%;
  position: absolute;
}
.page-details.norfolk .answer input,
.page-details.norfolk .answer textarea {
  width: 100%;
  font-size: 10px;
  font-weight: bold;
  color: #141414;
  position: absolute;
  background: transparent;
  overflow: hidden;
}
.page-details.norfolk .answer textarea {
  font-size: 10px;
  padding: 0 5px;
}
.page-details.norfolk .answer.monospace {
  font-family: monospace;
  letter-spacing: 7px;
}
.page-details.norfolk .answer .check-mark {
  position: absolute;
}
.page-details.norfolk .answer .check-mark::before, .page-details.norfolk .answer .check-mark::after {
  content: "";
  height: 2px;
  width: 15px;
  background: black;
  position: absolute;
  transform: rotate(45deg);
}
.page-details.norfolk .answer .check-mark::before {
  transform: rotate(-45deg);
}
.page-details.norfolk textarea.form-control {
  border: 0;
  resize: none;
  height: 100%;
}
